.bikes {
  display: flex;
  align-items: center;
  flex-direction: column;

  padding-top: 172px;
  padding-bottom: 175px;

  background-color: $color3;

  h2 {
    margin-bottom: 57px;

    text-align: center;

    color: $color1;

    font-size: 3rem;
    font-weight: 600;
    line-height: 3rem;
  }

  .bikes__wrapper {
    position: relative;
    left: 12px;

    display: flex;
    justify-content: space-between;

    width: 96.57%;
    margin: 0 auto;
  }

  figure {
    position: relative;

    width: 30.37%;

    &:not(:last-child) {
      margin-right: 4.44%;
    }
  }

  img {
    position: relative;
    z-index: 10;

    max-width: 100%;
    margin-bottom: 18px;
  }

  figcaption {
    text-align: center;
    letter-spacing: -0.5px;

    color: $black;

    font-size: 2.2rem;
    font-weight: 600;
    line-height: 3.4rem;
  }

  .bikes__andreas picture {
    &::before {
      position: absolute;
      bottom: 0;

      width: 180px;
      height: 180px;

      content: "";
      transform: translate(-60px, -160%);

      border: 27px solid $color2;
      border-radius: 50%;
    }
  }

  .bikes__eberhard {
    top: 29px;
  }

  .bikes__eberhard picture {
    &::before {
      position: absolute;
      z-index: 11;
      top: -13px;
      left: -15px;

      width: 37px;
      height: 37px;

      content: "";

      background-color: $black;
    }

    &::after {
      position: absolute;
      z-index: 11;
      right: -22px;
      bottom: 32px;

      width: 47px;
      height: 47px;

      content: "";

      border: 8px solid $color2;
    }
  }

  .bikes__roger {
    top: -9px;
  }

  .bikes__roger picture {
    &::before {
      position: absolute;
      z-index: 11;
      top: -33px;
      right: -95px;

      width: 95px;
      height: 95px;

      content: "";
      transform: translateX(-60px);

      border: 17px solid $color2;
      border-radius: 50%;
    }
  }

  @media (max-width: 1023px) {
    padding-top: 50px;
    padding-bottom: 0;

    h2 {
      margin-bottom: 27px;

      font-size: 2.4rem;
      line-height: 4.1rem;
    }

    .bikes__wrapper {
      left: 0;

      flex-wrap: wrap;
      justify-content: space-around;

      width: 91.53%;

      justify-items: center;
    }

    figure {
      width: 45.76%;
      margin-bottom: 57px;

      text-align: center;

      &:not(:last-child) {
        margin-right: 0;
      }
    }

    .bikes__andreas picture {
      &::before {
        width: 127px;
        height: 127px;

        transform: translate(-38px, -28px);

        border-width: 18px;
      }
    }

    .bikes__eberhard {
      top: 0;
    }

    .bikes__eberhard picture {
      &::before,
      &::after {
        display: none;
      }
    }

    .bikes__roger picture {
      &::before {
        top: -21px;
        right: -75px;

        width: 68px;
        height: 68px;

        border-width: 13px;
      }
    }
  }

  @media (max-width: 767px) {
    margin-bottom: 17px;
    padding-top: 22px;

    h2 {
      margin-bottom: 29px;

      font-size: 2.4rem;
      line-height: 4.1rem;
    }

    .bikes__wrapper {
      width: 100%;
    }

    figure {
      width: 100%;
      margin-bottom: 39px;
    }

    img {
      margin-bottom: 16px;
    }

    .bikes__andreas picture {
      &::before {
        transform: translate(-23px, -31px);
      }
    }

    .bikes__roger {
      top: 3px;
    }

    .bikes__roger picture {
      &::before {
        right: -65px;
      }
    }
  }
}
