.media {
  margin-bottom: 253px;

  background: $color2;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

  .media__wrapper {
    position: relative;
    top: 120px;

    display: flex;
    align-items: center;
    flex-direction: column;

    width: 100%;
    margin: 0 auto;
  }

  h2 {
    margin-bottom: 20px;
    margin-left: -9px;

    letter-spacing: -0.005em;

    color: $white;

    font-size: 3rem;
    font-weight: 600;
    line-height: 3rem;
  }

  p {
    max-width: 340px;
    margin-bottom: 56px;

    text-align: center;
    letter-spacing: -0.005em;

    opacity: 0.7;
    color: $white;

    font-size: 1.6rem;
    font-weight: 300;
    line-height: 2.8rem;
  }

  .media__video {
    position: relative;

    margin-bottom: 15px;

    text-align: center;

    img {
      width: 100%;
    }

    span {
      position: absolute;
      top: 50%;
      left: 50%;

      width: 65px;
      height: 65px;

      transform: translate(-50%, -50%);

      background-image: url("../../img/ic-play.svg");
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  @media (max-width: 1023px) {
    height: 414px;
    margin-bottom: 157px;

    background-color: $color8;

    .media__wrapper {
      top: 72px;
    }

    h2 {
      margin-left: 0;

      font-size: 2.4rem;
      line-height: 3rem;
    }

    p {
      max-width: 429px;
      margin-bottom: 39px;
    }

    .media__video {
      img {
        width: 502px;
        height: 282px;
      }

      span {
        width: 90px;
        height: 90px;
      }
    }
  }

  @media (max-width: 767px) {
    height: 380px;
    margin-bottom: 124px;
    padding-top: 14px;

    h2 {
      margin-left: -12px;
    }

    p {
      margin-bottom: 50px;
    }

    .media__video {
      img {
        width: 288px;
        height: 162px;
      }

      span {
        width: 65px;
        height: 65px;
      }
    }
  }
}
