.footer {
  display: flex;
  align-items: center;
  flex-direction: column;

  padding: 0 0 96px;

  h2 {
    margin-bottom: 28px;
    margin-left: -6px;

    text-align: center;
    letter-spacing: -0.005em;

    color: $color1;

    font-size: 3rem;
    font-weight: 600;
    line-height: 3rem;
  }

  p {
    width: 47.6%;
    margin: 0 auto 38px;

    text-align: center;

    color: rgba(0, 0, 0, 0.7);

    font-size: 1.7rem;
    line-height: 3.1rem;
  }

  .footer__map-wrapper {
    width: 82.08%;
    margin: 0 auto 37px;
  }

  .footer__map {
    width: 100%;
    height: 530px;
  }

  .footer__contacts {
    position: relative;
    left: -10px;

    display: flex;
    justify-content: space-between;

    width: 46.86%;
    margin: 0 auto;

    color: rgba(0, 0, 0, 0.5);

    a {
      display: flex;
      align-items: center;

      font-size: 1.6rem;
    }

    a::before {
      margin-right: 15px;

      content: "";

      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
  }

  .footer__email {
    line-height: 2.2rem;

    &::before {
      width: 26px;
      height: 20px;

      background-image: url("../img/ic-mail.svg");
    }
  }

  .footer__phone {
    line-height: 3.2rem;

    &::before {
      position: relative;

      width: 21px;
      height: 22px;

      background-image: url("../img/ic-phone.svg");
    }
  }

  @media (max-width: 1023px) {
    h2 {
      margin-bottom: 20px;
      margin-left: 2px;

      font-size: 2.4rem;
      line-height: 3rem;
    }

    p {
      width: 90%;
      margin-bottom: 18px;
    }

    .footer__map-wrapper {
      width: 100%;
      margin-bottom: 18px;
    }

    .footer__map {
      height: 545px;
    }

    .footer__contacts {
      left: -8px;

      width: 78%;
    }
  }

  @media (max-width: 767px) {
    .container {
      padding: 0;
    }

    h2 {
      font-size: 1.8rem;
      line-height: 3rem;
    }

    .footer__map-wrapper {
      margin-bottom: 32px;
    }

    .footer__map {
      height: 250px;
    }

    .footer__contacts {
      left: 0;

      align-items: center;
      flex-direction: column;

      width: 100%;
      padding: 0;

      .footer__email {
        width: 259px;
        margin-bottom: 20px;
      }

      .footer__phone {
        &::before {
          top: -3px;
          left: -7px;
        }
      }
    }
  }
}
