.about {
  display: flex;
  align-items: center;
  flex-direction: column;

  padding-top: 152px;
  padding-bottom: 131px;

  h2 {
    margin-bottom: 50px;
    margin-left: 9px;

    text-align: center;
    letter-spacing: -0.005em;

    color: $color1;

    font-size: 3rem;
    font-weight: 600;
    line-height: 3rem;
  }

  .about__facts {
    display: flex;
    justify-content: center;

    counter-reset: item;
  }

  .about__fact {
    display: flex;
    align-items: center;
    flex-direction: column;

    width: 22.55%;

    text-align: center;

    font-size: 2.2rem;
    line-height: 3.4rem;

    &:not(:last-child) {
      margin-right: 38px;
    }

    &::before {
      margin-bottom: 15px;

      content: "0" counter(item);
      counter-increment: item;
      letter-spacing: -1.27273px;

      color: $color2;

      font-size: 2.8rem;
      font-weight: bold;
      line-height: 3.8rem;
    }
  }

  @media (max-width: 1023px) {
    padding-top: 0;
    padding-bottom: 27px;

    h2 {
      margin-bottom: 32px;
      margin-left: 0;

      font-size: 2.4rem;
      line-height: 4.1rem;
    }

    .about__facts {
      flex-wrap: wrap;
      justify-content: space-between;

      width: 84%;
      margin: 0 auto;
    }

    .about__fact {
      width: 50%;
      max-width: 350px;
      margin-bottom: 43px;

      &:not(:last-child) {
        margin-right: 0;
      }

      &::before {
        margin-bottom: 15px;
      }
    }
  }

  @media (max-width: 767px) {
    padding-top: 20px;

    font-family: "Overpass", sans-serif;

    h2 {
      margin-bottom: 30px;
    }

    .about__facts {
      justify-content: center;

      width: 100%;
      margin-bottom: 84px;
    }

    .about__fact {
      width: 100%;

      font-size: 1.8rem;
      line-height: 3.4rem;

      &:first-child {
        margin-bottom: 34px;

        &::before {
          margin-bottom: 14px;
        }
      }

      &:nth-child(2) {
        margin-bottom: 56px;

        &::before {
          margin-bottom: 8px;
        }
      }

      &:nth-child(3) {
        margin-bottom: 60px;

        &::before {
          margin-bottom: 8px;
        }
      }

      &:last-child {
        margin-bottom: 0;

        &::before {
          margin-bottom: 8px;
        }
      }
    }
  }
}
